.preloader {
  width: 100%;
  height: 100%;
  z-index: 999;
  opacity: 1;
  visibility: visible;
  background-color: #fff;
  transition: all 1s;
  position: absolute;
  top: 0;
  left: 0;
}

.preloader .ld-roller {
  width: 80px;
  height: 80px;
  position: absolute;
  top: 500px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.preloader.done {
  opacity: 0;
  visibility: hidden;
}

.preloader .ld-roller div {
  transform-origin: 40px 40px;
  animation: ld-roller 1.5s cubic-bezier(.5, 0, .5, 1) infinite;
}

.preloader .ld-roller div:after {
  content: " ";
  width: 7px;
  height: 7px;
  background: #ff0101;
  border-radius: 50%;
  margin: -4px 0 0 -4px;
  display: block;
  position: absolute;
}

.preloader .ld-roller div:nth-child(1) {
  animation-delay: -.036s;
}

.preloader .ld-roller div:nth-child(1):after {
  top: 63px;
  left: 63px;
}

.preloader .ld-roller div:nth-child(2) {
  animation-delay: -.072s;
}

.preloader .ld-roller div:nth-child(2):after {
  top: 68px;
  left: 56px;
}

.preloader .ld-roller div:nth-child(3) {
  animation-delay: -.108s;
}

.preloader .ld-roller div:nth-child(3):after {
  top: 71px;
  left: 48px;
}

.preloader .ld-roller div:nth-child(4) {
  animation-delay: -.144s;
}

.preloader .ld-roller div:nth-child(4):after {
  top: 72px;
  left: 40px;
}

.preloader .ld-roller div:nth-child(5) {
  animation-delay: -.18s;
}

.preloader .ld-roller div:nth-child(5):after {
  top: 71px;
  left: 32px;
}

.preloader .ld-roller div:nth-child(6) {
  animation-delay: -.216s;
}

.preloader .ld-roller div:nth-child(6):after {
  top: 68px;
  left: 24px;
}

.preloader .ld-roller div:nth-child(7) {
  animation-delay: -.252s;
}

.preloader .ld-roller div:nth-child(7):after {
  top: 63px;
  left: 17px;
}

.preloader .ld-roller div:nth-child(8) {
  animation-delay: -.288s;
}

.preloader .ld-roller div:nth-child(8):after {
  top: 56px;
  left: 12px;
